@import url("https://fonts.googleapis.com/css2?family=Karla:ital,wght@0,400;0,700;1,400;1,700&family=Lora:ital,wght@0,400;0,700;1,400;1,700&display=swap");
html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Karla", sans-serif;
  font-size: 16px;
  background-color: var(--main-bg);
  color: var(--color-text-default);
}

a {
  text-decoration: none;
  color: var(--color-accent);
}

a:hover {
  text-decoration: none;
}

header .nav-link {
  font-family: Karla, sans-serif;
  color: var(--color-accent);
}
header .navbar .nav-link {
  font-family: Karla, sans-serif;
  color: var(--color-accent);
  text-transform: uppercase;
}
header .nav-link:hover {
  color: var(--color-text-default);
}
header .navbar {
  min-height: 100px;
}

main {
  border-top: var(--color-border) 4px solid;
  border-bottom: var(--color-border) 4px solid;
}

nav {
  border-bottom: var(--color-border) 1px solid;
}

/*footer*/
footer .title {
  font-weight: 700;
  text-transform: uppercase;
}
footer li {
  list-style-type: none;
}
footer nav {
  text-transform: none;
  font-weight: 400;
  float: left;
  padding: 0;
  border: 0;
}

h1, h2, h3 {
  font-weight: 700;
  line-height: 1.1;
  margin-top: 0;
}

p {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

/* rootColors lightMode */
@media (prefers-color-scheme: light) {
  :root {
    --background-color: #eaeaea;
    --separator: linear-gradient(
      65deg,
      #ffef26,
      #fec800,
      #f19135,
      #e85e31,
      #e3312d
    );
    --main-bg: #eaeaea;
    --widget-bg: #fff;
    --bg-white: #fff;
    --color-title: #212121;
    --color-text: #212121;
    --color-footer-title: #e3312d;
    --color-link: #212121;
    --color-link-hover: #e3312d;
    --color-header: #fff;
    --color-header-footer-border: #e2e1e1;
    --color-border: #eaeaea;
    --color-display-border: #fff;
    --display-h3: #fff;
    --plyr-audio-controls-background: #fff;
  } /* :root light/default */
  footer {
    min-height: 375px;
    background-image: url("../images/bg_footer-light.jpg");
  }
} /* @media light */
/* rootColors darkMode */
@media (prefers-color-scheme: dark) {
  :root {
    --separator: linear-gradient(65deg, #12151a, #222b3c, #111619);
    --main-bg: #0d1117;
    --navbar-bg: #161b22;
    --widget-bg: #161b22;
    --color-border: #21262d;
    --color-text-default: #8b949e;
    --color-accent: #ec1a62;
    --plyr-audio-controls-background: #0e1011;
  } /* :root dark */
  footer {
    min-height: 375px;
    background-image: url("../images/bg_footer-dark.jpg");
  }
} /* @media dark */
.cus-border-bottom {
  border-bottom: 1px var(--color-border) solid;
}

.main-body {
  padding: 15px;
}

.card {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;
  background-color: #fff;
  background-clip: border-box;
  border: 0 solid rgba(0, 0, 0, 0.125);
  border-radius: 0.25rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}

.card-body {
  flex: 1 1 auto;
  min-height: 1px;
  padding: 1rem;
}

.gutters-sm {
  margin-right: -8px;
  margin-left: -8px;
}

.gutters-sm > .col, .gutters-sm > [class*=col-] {
  padding-right: 8px;
  padding-left: 8px;
}

.mb-3, .my-3 {
  margin-bottom: 1rem !important;
}

.bg-gray-300 {
  background-color: #e2e8f0;
}

.h-100 {
  height: 100% !important;
}

.shadow-none {
  box-shadow: none !important;
}

.widget h3 {
  font-size: 24px;
  font-family: "Karla", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  background-color: var(--widget-bg);
}
.widget .song_title {
  text-transform: uppercase;
  font-weight: bold;
}

.displayShows {
  background-size: cover;
  padding: 10px 10px 10px 20px;
  font-family: Karla, sans-serif;
}
.displayShows h3 {
  font-size: 24px;
  font-family: "Karla", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
}
.displayShows .description {
  padding: 10px;
}

.events {
  width: 100%;
  height: 150px;
  font-family: Karla, sans-serif;
}

.player {
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: center;
}

.comingSoon {
  text-align: center;
  height: 50px;
  padding: 10px 0 10px 0;
  border-top: 4px #999 solid;
  background-color: var(--bg-white);
}

.post-content {
  background-color: #fff;
  border-bottom: 4px var(--color-title) solid;
  margin-bottom: 10px;
  padding-top: 20px;
  font-family: Lora, "serif";
}
.post-content blockquote {
  margin: 50px 0 50px 50px;
  padding: 0 50px 0 50px;
  border-left: 4px var(--color-text) solid;
  font-size: 2.5rem;
}
.post-content .img-single {
  width: 100%;
  margin: 20px 0 20px 0;
}
.post-content .gallery {
  margin: 20px 0 20px 0;
}
.post-content .img-gallery {
  width: 200px;
  height: 200px;
  margin: 10px 10px 10px 10px;
}
.post-content .figure-caption {
  margin-bottom: 20px;
  padding: 10px 0 10px 0;
  line-height: 0.8rem;
  border-top: #000 1px solid;
  border-bottom: #000 1px solid;
}
.post-content li {
  margin: 10px 0 10px 0;
}
.post-content .post-title {
  background-color: #fff;
  padding: 20px;
  color: #111619;
  text-transform: uppercase;
  width: 50%;
  margin-left: 25%;
}

.posts-img {
  min-height: 600px;
  background-size: cover;
}

.post-title {
  background-color: #fff;
  padding: 20px;
  color: #111619;
  text-transform: uppercase;
  width: 50%;
  margin-left: 25%;
}

.form-signin {
  max-width: 330px;
  padding: 15px;
}
.form-signin .form-floating:focus-within {
  z-index: 2;
}
.form-signin input[type=email] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type=password] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

.bd-placeholder-img {
  font-size: 1.125rem;
  text-anchor: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}

@media (min-width: 768px) {
  .bd-placeholder-img-lg {
    font-size: 3.5rem;
  }
}
.b-example-divider {
  height: 3rem;
  background-color: rgba(0, 0, 0, 0.1);
  border: solid rgba(0, 0, 0, 0.15);
  border-width: 1px 0;
  box-shadow: inset 0 0.5em 1.5em rgba(0, 0, 0, 0.1), inset 0 0.125em 0.5em rgba(0, 0, 0, 0.15);
}

.b-example-vr {
  flex-shrink: 0;
  width: 1.5rem;
  height: 100vh;
}

.bi {
  vertical-align: -0.125em;
  fill: currentColor;
}

.nav-scroller {
  position: relative;
  z-index: 2;
  height: 2.75rem;
  overflow-y: hidden;
}

.nav-scroller .nav {
  display: flex;
  flex-wrap: nowrap;
  padding-bottom: 1rem;
  margin-top: -1px;
  overflow-x: auto;
  text-align: center;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;
}